"use client"

import { ProtectedPage } from "@/components/auth/ProtectedPage"

import { useState, useEffect, useMemo, useRef } from "react"
import { useRouter } from "next/navigation"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { useAppSelector, useAppDispatch } from "@/lib/store/hooks"
import { fetchReplicationEvents, fetchMonitoringMetrics, setSelectedPipeline } from "@/lib/store/slices/monitoringSlice"
import { fetchPipelines } from "@/lib/store/slices/pipelineSlice"
import { wsClient } from "@/lib/websocket/client"
import { formatDistanceToNow } from "date-fns"
import { Loader2, Activity, Database, AlertCircle, CheckCircle, RefreshCw, Eye, RotateCw, ArrowRight, Zap, Clock } from "lucide-react"
import { apiClient } from "@/lib/api/client"
import { PageHeader } from "@/components/ui/page-header"
import { LineChart, Line, BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Area, AreaChart } from "recharts"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"

export default function MonitoringPage() {
  const router = useRouter()
  const dispatch = useAppDispatch()
  const { isAuthenticated, isLoading: authLoading } = useAppSelector((state) => state.auth)
  const { events, metrics, selectedPipelineId, isLoading } = useAppSelector((state) => state.monitoring)
  const { pipelines } = useAppSelector((state) => state.pipelines)
  const [mounted, setMounted] = useState(false)
  const [currentPage, setCurrentPage] = useState(1)
  const rowsPerPage = 10
  const [retryingEventId, setRetryingEventId] = useState<string | null>(null)
  const [isRefreshing, setIsRefreshing] = useState(false)
  const [wsConnected, setWsConnected] = useState(false)
  const [wsAvailable, setWsAvailable] = useState(true) // Default to true to show status

  // Handle client-side mounting
  useEffect(() => {
    setMounted(true)
  }, [])

  useEffect(() => {
    if (!authLoading && !isAuthenticated) {
      router.push("/auth/login")
    }
  }, [isAuthenticated, authLoading, router])

  // Track if user has explicitly selected "All Pipelines" to prevent auto-selection
  const userSelectedAllRef = useRef(false)

  // Auto-select first pipeline when pipelines load and selectedPipelineId is null
  useEffect(() => {
    if (isAuthenticated && pipelines.length > 0 && !selectedPipelineId && !userSelectedAllRef.current) {
      const firstPipeline = pipelines[0]
      if (firstPipeline && firstPipeline.id) {
        const pipelineId = !isNaN(Number(firstPipeline.id)) ? Number(firstPipeline.id) : String(firstPipeline.id)
        dispatch(setSelectedPipeline(pipelineId))
      }
    }
  }, [dispatch, isAuthenticated, pipelines, selectedPipelineId])

  // Fetch data on mount
  useEffect(() => {
    if (isAuthenticated) {
      dispatch(fetchPipelines())
      dispatch(fetchReplicationEvents({ limit: 100000, todayOnly: false }))

      wsClient.connect()
      setWsConnected(wsClient.isConnected())
      setWsAvailable(wsClient.isAvailable())
    }
  }, [dispatch, isAuthenticated])

  // Check WebSocket connection status
  useEffect(() => {
    if (!isAuthenticated) return

    const updateWsStatus = () => {
      const connected = wsClient.isConnected()
      const available = wsClient.isAvailable()
      setWsConnected(connected)
      setWsAvailable(available)
    }

    const wsStatusInterval = setInterval(updateWsStatus, 2000)
    const unsubscribeStatus = wsClient.onStatusChange(updateWsStatus)
    updateWsStatus()

    return () => {
      clearInterval(wsStatusInterval)
      unsubscribeStatus()
    }
  }, [isAuthenticated])

  // Track last fetched pipeline ID to prevent unnecessary refetches
  const lastFetchedPipelineIdRef = useRef<string | number | null>(null)
  const isInitialMountRef = useRef(true)

  const stableSelectedPipelineId = useMemo(() => {
    if (!selectedPipelineId) return null
    return String(selectedPipelineId)
  }, [selectedPipelineId])

  // Fetch events and metrics when pipeline is selected
  useEffect(() => {
    if (!isAuthenticated) return

    const normalizedId = stableSelectedPipelineId ? stableSelectedPipelineId : null

    if (isInitialMountRef.current) {
      isInitialMountRef.current = false
    } else {
      if (lastFetchedPipelineIdRef.current === normalizedId && normalizedId !== null) {
        return
      }
    }

    lastFetchedPipelineIdRef.current = normalizedId

    if (normalizedId) {
      const pipelineId = !isNaN(Number(normalizedId)) ? Number(normalizedId) : normalizedId
      if (pipelineId && pipelineId !== 'null' && pipelineId !== 'undefined') {
        dispatch(fetchReplicationEvents({ pipelineId, limit: 100000, todayOnly: false }))
        dispatch(fetchMonitoringMetrics({ pipelineId })).catch(err => {
          console.error("Error fetching metrics:", err)
        })
      }
    } else {
      dispatch(fetchReplicationEvents({ limit: 100000, todayOnly: false }))
    }
  }, [dispatch, isAuthenticated, stableSelectedPipelineId])

  // Auto-refresh events
  const selectedPipelineIdRef = useRef<string | number | null>(null)

  useEffect(() => {
    selectedPipelineIdRef.current = stableSelectedPipelineId
  }, [stableSelectedPipelineId])

  useEffect(() => {
    if (!isAuthenticated) return

    const interval = setInterval(() => {
      const currentPipelineId = selectedPipelineIdRef.current
      if (currentPipelineId) {
        const pipelineId = !isNaN(Number(currentPipelineId)) ? Number(currentPipelineId) : String(currentPipelineId)
        if (pipelineId && pipelineId !== 'null' && pipelineId !== 'undefined') {
          dispatch(fetchReplicationEvents({ pipelineId, limit: 1000, todayOnly: false }))
          dispatch(fetchMonitoringMetrics({ pipelineId }))
        }
      } else {
        dispatch(fetchReplicationEvents({ limit: 1000, todayOnly: false }))
      }
    }, 5000)

    return () => clearInterval(interval)
  }, [dispatch, isAuthenticated])

  // Subscribe to pipeline room
  useEffect(() => {
    if (!isAuthenticated || !wsClient.isConnected()) return

    if (selectedPipelineId) {
      const pipelineId = !isNaN(Number(selectedPipelineId)) ? Number(selectedPipelineId) : String(selectedPipelineId)
      wsClient.subscribePipeline(pipelineId)
      return () => wsClient.unsubscribePipeline(pipelineId)
    } else {
      const pipelinesArray = Array.isArray(pipelines) ? pipelines : []
      if (pipelinesArray.length > 0) {
        const pipelineIds = pipelinesArray
          .filter(p => p.id)
          .map(p => !isNaN(Number(p.id)) ? Number(p.id) : String(p.id))

        pipelineIds.forEach(id => wsClient.subscribePipeline(id))
        return () => pipelineIds.forEach(id => wsClient.unsubscribePipeline(id))
      }
    }
  }, [isAuthenticated, selectedPipelineId, pipelines, wsClient])

  const filteredEvents = useMemo(() => {
    const eventsArray = Array.isArray(events) ? events : []
    if (!selectedPipelineId) return eventsArray
    return eventsArray.filter(e => String(e.pipeline_id) === String(selectedPipelineId))
  }, [events, selectedPipelineId])

  const chartData = useMemo(() => {
    const eventsArray = Array.isArray(events) ? events : []
    const metricsArray = Array.isArray(metrics) ? metrics : []

    const now = new Date()
    // Create 12 buckets of 5 minutes for the last hour
    const buckets = Array.from({ length: 12 }, (_, i) => {
      const bucketTime = new Date(now.getTime() - (11 - i) * 5 * 60 * 1000)
      return bucketTime
    })

    return buckets.map(bucket => {
      const bucketStart = new Date(bucket)
      const bucketEnd = new Date(bucket.getTime() + 5 * 60 * 1000)

      const bucketEvents = eventsArray.filter(e => {
        try {
          const eventTime = new Date(e.created_at || e.source_commit_time || Date.now())
          return eventTime >= bucketStart && eventTime < bucketEnd
        } catch { return false }
      })

      return {
        time: bucket.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }),
        events: bucketEvents.length,
        latency: bucketEvents.reduce((sum, e) => sum + (e.processing_time_ms || 0), 0) / (bucketEvents.length || 1)
      }
    })
  }, [events, metrics])

  const stats = useMemo(() => {
    const totalEvents = filteredEvents.length
    const appliedEvents = filteredEvents.filter(e => e.status === 'applied' || e.status === 'success').length
    const pendingEvents = filteredEvents.filter(e => e.status === 'captured' || e.status === 'pending').length
    const failedEvents = filteredEvents.filter(e => e.status === 'failed' || e.status === 'error').length

    // Calculate average latency
    const eventsWithLatency = filteredEvents.filter(e => typeof e.processing_time_ms === 'number')
    const avgLatency = eventsWithLatency.length > 0
      ? Math.round(eventsWithLatency.reduce((sum, e) => sum + (e.processing_time_ms || 0), 0) / eventsWithLatency.length)
      : 0

    return { totalEvents, appliedEvents, pendingEvents, failedEvents, avgLatency }
  }, [filteredEvents])

  const handleRefresh = () => {
    setIsRefreshing(true)
    const pipelineId = selectedPipelineId
      ? (!isNaN(Number(selectedPipelineId)) ? Number(selectedPipelineId) : String(selectedPipelineId))
      : undefined

    Promise.all([
      dispatch(fetchReplicationEvents({
        pipelineId,
        limit: 100000,
        todayOnly: false
      })),
      pipelineId ? dispatch(fetchMonitoringMetrics({ pipelineId })) : Promise.resolve()
    ]).finally(() => {
      setTimeout(() => setIsRefreshing(false), 500)
    })
  }

  // Pagination logic
  const totalPages = Math.ceil(filteredEvents.length / rowsPerPage)
  const paginatedEvents = filteredEvents.slice((currentPage - 1) * rowsPerPage, currentPage * rowsPerPage)

  if (!mounted) return null

  return (
    <ProtectedPage path="/monitoring" requiredPermission="view_monitoring">
      <div className="p-6 space-y-6 max-w-[1600px] mx-auto">
        <PageHeader
          title="Real-time Monitoring"
          subtitle="Live CDC event stream and performance metrics"
          icon={Activity}
          action={
            <div className="flex items-center gap-3">
              <div className="flex items-center gap-2 px-3 py-1.5 bg-card border border-border rounded-md shadow-sm">
                <div className={`w-2 h-2 rounded-full ${wsConnected ? 'bg-success animate-pulse' : 'bg-error'}`} />
                <span className="text-xs font-medium text-foreground-muted">
                  {wsConnected ? 'Live Updates' : 'Disconnected'}
                </span>
              </div>
              <Select
                value={selectedPipelineId ? String(selectedPipelineId) : "all"}
                onValueChange={(value) => {
                  if (value === "all") {
                    userSelectedAllRef.current = true
                    dispatch(setSelectedPipeline(null))
                  } else {
                    userSelectedAllRef.current = false
                    const id = !isNaN(Number(value)) ? Number(value) : value
                    dispatch(setSelectedPipeline(id))
                  }
                }}
              >
                <SelectTrigger className="w-[200px] h-9 text-xs">
                  <SelectValue placeholder="All Pipelines" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All Pipelines</SelectItem>
                  {pipelines.map(p => (
                    <SelectItem key={p.id} value={String(p.id)}>{p.name}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
              <Button
                variant="outline"
                size="sm" // Use small button
                className="h-9 gap-2" // Adjust height
                onClick={handleRefresh}
                disabled={isRefreshing || isLoading}
              >
                <RefreshCw className={`w-3.5 h-3.5 ${isRefreshing ? 'animate-spin' : ''}`} />
                Refresh
              </Button>
            </div>
          }
        />

        {/* Stats Grid - Compact & Clean */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4">
          <Card className="p-4 bg-card border-border shadow-sm hover:shadow-md transition-all group">
            <div className="flex items-center gap-3 mb-2">
              <div className="p-2 bg-primary/10 rounded-lg group-hover:scale-110 transition-transform">
                <Database className="w-4 h-4 text-primary" />
              </div>
              <span className="text-xs font-medium text-foreground-muted uppercase tracking-wide">Total Events</span>
            </div>
            <div className="flex items-baseline gap-2">
              <span className="text-2xl font-bold text-foreground">{stats.totalEvents.toLocaleString()}</span>
              {wsConnected && <span className="flex h-2 w-2 rounded-full bg-primary animate-pulse ml-auto" />}
            </div>
          </Card>

          <Card className="p-4 bg-card border-border shadow-sm hover:shadow-md transition-all group">
            <div className="flex items-center gap-3 mb-2">
              <div className="p-2 bg-success/10 rounded-lg group-hover:scale-110 transition-transform">
                <CheckCircle className="w-4 h-4 text-success" />
              </div>
              <span className="text-xs font-medium text-foreground-muted uppercase tracking-wide">Applied</span>
            </div>
            <div className="flex items-baseline gap-2">
              <span className="text-2xl font-bold text-foreground">{stats.appliedEvents.toLocaleString()}</span>
              <span className="text-xs text-success ml-auto">
                {stats.totalEvents > 0 ? Math.round((stats.appliedEvents / stats.totalEvents) * 100) : 0}%
              </span>
            </div>
          </Card>

          <Card className="p-4 bg-card border-border shadow-sm hover:shadow-md transition-all group">
            <div className="flex items-center gap-3 mb-2">
              <div className="p-2 bg-warning/10 rounded-lg group-hover:scale-110 transition-transform">
                <RotateCw className="w-4 h-4 text-warning" />
              </div>
              <span className="text-xs font-medium text-foreground-muted uppercase tracking-wide">Pending</span>
            </div>
            <div className="flex items-baseline gap-2">
              <span className="text-2xl font-bold text-foreground">{stats.pendingEvents.toLocaleString()}</span>
            </div>
          </Card>

          <Card className="p-4 bg-card border-border shadow-sm hover:shadow-md transition-all group">
            <div className="flex items-center gap-3 mb-2">
              <div className="p-2 bg-info/10 rounded-lg group-hover:scale-110 transition-transform">
                <Clock className="w-4 h-4 text-info" />
              </div>
              <span className="text-xs font-medium text-foreground-muted uppercase tracking-wide">Avg Latency</span>
            </div>
            <div className="flex items-baseline gap-2">
              <span className="text-2xl font-bold text-foreground">{stats.avgLatency}ms</span>
            </div>
          </Card>

          <Card className="p-4 bg-card border-border shadow-sm hover:shadow-md transition-all group">
            <div className="flex items-center gap-3 mb-2">
              <div className="p-2 bg-error/10 rounded-lg group-hover:scale-110 transition-transform">
                <AlertCircle className="w-4 h-4 text-error" />
              </div>
              <span className="text-xs font-medium text-foreground-muted uppercase tracking-wide">Failed</span>
            </div>
            <div className="flex items-baseline gap-2">
              <span className="text-2xl font-bold text-foreground">{stats.failedEvents.toLocaleString()}</span>
              {stats.failedEvents > 0 && <span className="text-xs text-error font-medium ml-auto">Alert</span>}
            </div>
          </Card>
        </div>

        {/* Charts & Feed */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* Charts Column */}
          <div className="lg:col-span-2 space-y-6">
            <Card className="p-5 border-border shadow-sm">
              <div className="flex items-center justify-between mb-6">
                <div>
                  <h3 className="text-sm font-bold text-foreground">Event Throughput</h3>
                  <p className="text-xs text-foreground-muted">Events processed per 5 minutes (last hour)</p>
                </div>
                <div className="flex items-center gap-2">
                  <Badge variant="outline" className="text-[10px] font-normal">Real-time</Badge>
                </div>
              </div>
              <div className="h-[250px]">
                <ResponsiveContainer width="100%" height="100%">
                  <AreaChart data={chartData} margin={{ top: 10, right: 10, left: -20, bottom: 0 }}>
                    <defs>
                      <linearGradient id="colorEvents" x1="0" y1="0" x2="0" y2="1">
                        <stop offset="5%" stopColor="var(--primary)" stopOpacity={0.2} />
                        <stop offset="95%" stopColor="var(--primary)" stopOpacity={0} />
                      </linearGradient>
                    </defs>
                    <CartesianGrid strokeDasharray="3 3" vertical={false} stroke="var(--border)" opacity={0.4} />
                    <XAxis dataKey="time" stroke="var(--foreground-muted)" tickLine={false} axisLine={false} tick={{ fontSize: 10 }} dy={10} />
                    <YAxis stroke="var(--foreground-muted)" tickLine={false} axisLine={false} tick={{ fontSize: 10 }} />
                    <Tooltip
                      contentStyle={{
                        backgroundColor: 'var(--popover)',
                        borderColor: 'var(--border)',
                        fontSize: '12px',
                        borderRadius: '8px',
                        boxShadow: '0 4px 12px rgba(0,0,0,0.1)'
                      }}
                    />
                    <Area type="monotone" dataKey="events" stroke="var(--primary)" fill="url(#colorEvents)" strokeWidth={2} activeDot={{ r: 6 }} />
                  </AreaChart>
                </ResponsiveContainer>
              </div>
            </Card>

            <Card className="p-5 border-border shadow-sm">
              <div className="flex items-center justify-between mb-6">
                <div>
                  <h3 className="text-sm font-bold text-foreground">Processing Latency</h3>
                  <p className="text-xs text-foreground-muted">Average message processing time (ms)</p>
                </div>
              </div>
              <div className="h-[200px]">
                <ResponsiveContainer width="100%" height="100%">
                  <BarChart data={chartData} margin={{ top: 10, right: 10, left: -20, bottom: 0 }}>
                    <CartesianGrid strokeDasharray="3 3" vertical={false} stroke="var(--border)" opacity={0.4} />
                    <XAxis dataKey="time" stroke="var(--foreground-muted)" tickLine={false} axisLine={false} tick={{ fontSize: 10 }} dy={10} />
                    <YAxis stroke="var(--foreground-muted)" tickLine={false} axisLine={false} tick={{ fontSize: 10 }} />
                    <Tooltip
                      cursor={{ fill: 'var(--muted)', opacity: 0.2 }}
                      contentStyle={{
                        backgroundColor: 'var(--popover)',
                        borderColor: 'var(--border)',
                        fontSize: '12px',
                        borderRadius: '8px',
                        boxShadow: '0 4px 12px rgba(0,0,0,0.1)'
                      }}
                    />
                    <Bar dataKey="latency" fill="var(--info)" radius={[4, 4, 0, 0]} maxBarSize={40} />
                  </BarChart>
                </ResponsiveContainer>
              </div>
            </Card>
          </div>

          {/* Live Feed Column */}
          <Card className="flex flex-col h-[600px] border-border shadow-sm p-0 bg-card overflow-hidden">
            <div className="p-4 border-b border-border bg-muted/30 flex justify-between items-center">
              <div>
                <h3 className="text-sm font-bold text-foreground">Live Feed</h3>
                <p className="text-xs text-foreground-muted">Checking for updates...</p>
              </div>
              <Badge variant={wsConnected ? "default" : "destructive"} className="text-[10px] h-5 px-1.5">
                {wsConnected ? 'Connected' : 'Offline'}
              </Badge>
            </div>

            <div className="flex-1 overflow-auto p-0 text-sm">
              {paginatedEvents.length > 0 ? (
                <div className="divide-y divide-border/50">
                  {paginatedEvents.map((event) => (
                    <div key={event.id} className="p-3 hover:bg-surface-hover transition-colors group">
                      <div className="flex items-center justify-between mb-1.5">
                        <span className={`text-[10px] uppercase font-bold px-1.5 py-0.5 rounded border ${event.operation === 'INSERT' ? 'bg-success/10 text-success border-success/20' :
                            event.operation === 'UPDATE' ? 'bg-info/10 text-info border-info/20' :
                              event.operation === 'DELETE' ? 'bg-error/10 text-error border-error/20' :
                                'bg-muted text-foreground-muted border-border'
                          }`}>
                          {event.operation}
                        </span>
                        <span className="text-[10px] text-foreground-muted">
                          {event.created_at ? formatDistanceToNow(new Date(event.created_at), { addSuffix: true }) : 'just now'}
                        </span>
                      </div>
                      <div className="mb-1">
                        <p className="font-mono text-xs text-foreground truncate">{event.table_name}</p>
                      </div>
                      <div className="flex items-center justify-between">
                        <div className="flex items-center gap-2">
                          <span className={`w-1.5 h-1.5 rounded-full ${event.status === 'applied' || event.status === 'success' ? 'bg-success' :
                              event.status === 'failed' || event.status === 'error' ? 'bg-error' : 'bg-warning'
                            }`} />
                          <span className="text-[10px] text-foreground-muted uppercase tracking-wider">{event.status}</span>
                        </div>
                        <Button variant="ghost" size="sm" className="h-6 w-6 p-0 opacity-0 group-hover:opacity-100 transition-opacity">
                          <Eye className="w-3 h-3 text-foreground-muted" />
                        </Button>
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <div className="flex flex-col items-center justify-center h-full text-foreground-muted p-8 text-center">
                  <Activity className="w-8 h-8 opacity-20 mb-2" />
                  <p className="text-xs">No events found</p>
                </div>
              )}
            </div>

            <div className="p-3 border-t border-border bg-muted/10 flex justify-between items-center text-xs">
              <span className="text-foreground-muted">
                Page {currentPage} of {totalPages || 1}
              </span>
              <div className="flex gap-1">
                <Button
                  variant="outline"
                  size="sm"
                  className="h-6 px-2 text-[10px]"
                  onClick={() => setCurrentPage(c => Math.max(1, c - 1))}
                  disabled={currentPage === 1}
                >Prev</Button>
                <Button
                  variant="outline"
                  size="sm"
                  className="h-6 px-2 text-[10px]"
                  onClick={() => setCurrentPage(c => Math.min(totalPages, c + 1))}
                  disabled={currentPage >= totalPages}
                >Next</Button>
              </div>
            </div>
          </Card>
        </div>
      </div>
    </ProtectedPage>
  )
}
